import logo from './poloco.svg';
import sun from "./sun.svg";
import pyramides from "./pyramides.svg"
import montagnes from "./montagnes.svg"
import rainbow from "./rainbow.svg"
import forme1 from "./formes1.svg"
import forme2 from "./forme2.svg"
import poteaux from "./poteaux.svg"
import './App.scss';

import {
    MouseParallaxChild,
    MouseParallaxContainer
} from "react-parallax-mouse";

let MINI = 0.012

function App() {
  return (
    <div className="App">
        <img src={sun} className="sun" alt="sun" />
        <MouseParallaxContainer resetOnLeave className='App-header'>
                <img src={logo} className="App-logo" alt="logo" />
                <MouseParallaxChild className="rainbow" factorX={4 * MINI} factorY={0.01} updateStyles={{}}>
                    <img src={rainbow}  alt="logo" />
                </MouseParallaxChild>
                <MouseParallaxChild className="forme1" factorX={5 * MINI} factorY={0.01} updateStyles={{}}>
                    <img src={forme1} alt="logo" />
                </MouseParallaxChild>
                <MouseParallaxChild className="forme2" factorX={4 * MINI} factorY={0.01} updateStyles={{}}>
                    <img src={forme2} alt="logo" />
                </MouseParallaxChild>
                <MouseParallaxChild className="pyramides" factorX={MINI / 2} factorY={0.01} updateStyles={{}}>
                    <img src={pyramides} alt="logo" />
                </MouseParallaxChild>
                <MouseParallaxChild className="poteaux" factorX={2 * MINI} factorY={0.01} updateStyles={{}}>
                    <img src={poteaux} alt="logo" />
                </MouseParallaxChild>
                <MouseParallaxChild className="poteaux2" factorX={10 * MINI} factorY={0.02} updateStyles={{}}>
                    <img src={poteaux} alt="logo" />
                </MouseParallaxChild>
                <MouseParallaxChild className="montagne" factorX={MINI} factorY={0.02} updateStyles={{}}>
                    <img src={montagnes} alt="logo" />
                </MouseParallaxChild>
        </MouseParallaxContainer>
    </div>
  );
}

export default App;
